import uniLoadMore from "@/components/uni-load-more/uni-load-more.vue";
let _this,timer;
export default {
	components:{
		uniLoadMore
	},
	data() {
		return {
			loadingType:'more',
			items:[],
			pageNum: 1,
			pageSize: 20,
		};
	},
onReachBottom:function(){
	 if (timer != null) {
	            clearTimeout(timer);
	        }
	   timer = setTimeout(function() {
	        _this.fetchList();
	        }, 1000);
},
onLoad:function(){
	_this = this
	this.fetchList()
},
	methods:{
		async fetchList(){
			if (this.loadingType == ''){
				return
			}
			let data ={
				pageNum: this.pageNum,
				pageSize: this.pageSize
			}
			
			const res = await this.$http.get('/message/list',{data: data})
			if (res.data.data.isLastPage){
				this.loadingType = ''
			}else {
				this.pageSize += 20
			}
			this.items = res.data.data.list
			console.log(res)
		},
	}
}
